import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { ErrorGenericService } from './error.generic.service';

@Injectable({
    providedIn: 'root',
})

export class RestaurantService {
    constructor(private http: HttpClient, public errorGenericService: ErrorGenericService) {
    }

    configUrl = 'http://localhost:3000/restaurant';

    getRestInfo() {
        return this.http.get(this.configUrl, { observe: 'response' })
            .pipe(
                catchError(this.errorGenericService.handleError)
            );
    }

    getRestInfoByID(restaurantId: string) {
        return this.http.get(this.configUrl + '/' + restaurantId, { observe: 'response' })
            .pipe(
                catchError(this.errorGenericService.handleError)
            );
    }
}

