import {
    HTTP_INTERCEPTORS, HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor,
    HttpRequest
} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { empty } from 'rxjs';
import { throwError } from 'rxjs';
import { Router } from '@angular/router';
import { catchError } from 'rxjs/operators';

@Injectable()
export class HttpErrorInterceptor implements HttpInterceptor {
    constructor(private router: Router) {
    }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(req)
            .pipe(
                catchError((error => {
                    if (error instanceof HttpErrorResponse && error.status === 404) {
                        this.router.navigateByUrl('/404', { replaceUrl: true });

                        // tslint:disable-next-line: deprecation
                        return empty();
                    } else {
                        return throwError(error);
                    }
                })));
    }
}

export const HttpErrorInterceptorProvider = {
    provide: HTTP_INTERCEPTORS,
    useClass: HttpErrorInterceptor,
    multi: true,
};


//This interceptor will catch any error encountered by a HttpClient anywhere in your application. 
//I then add some condition to filter 404 errors, and, after redirecting the user to the /not-found page, 
//returns an empty Observable so that the error handling in the BlogPostShowComponent is not also triggered.

// source https://medium.com/@antwaneb/catch-and-handle-all-404-errors-from-your-webservice-in-angular-5-2269b717d78f