import { Component, OnInit } from '@angular/core';

import { ActivatedRoute } from '@angular/router';
import { RestaurantService } from '../services/restaurant.service';
import { isNgTemplate } from '@angular/compiler';

@Component({
  selector: 'app-restaurant-profile',
  templateUrl: './restaurant-profile.component.html',
  styleUrls: ['./restaurant-profile.component.css']
})
export class RestaurantProfileComponent implements OnInit {
  restaurant: any;
  errorMessage: any;
  products: any;
  constructor(restaurantService: RestaurantService, private route: ActivatedRoute) {
    this.route.paramMap.subscribe(params => {
      restaurantService.getRestInfoByID(params.get('restaurantId'))
        .subscribe(resp => {
          this.restaurant = resp.body;
        });
    });
  }

  ngOnInit() {
    // this.route.paramMap.subscribe(params => {
    //   console.log(params)
    //   this.restaurant = RestaurantService
    // });
  }

}
